<template>
  <div class="myMerchant">
    <div class="flex myTitle">
      <p @click="goback">
        <i style="font-size:16px;" class="el-icon-arrow-left"></i>
      </p>
      <p style="font-weight: 600;">我的商户</p>
      <p></p>
    </div>
    <!--  -->
    <div>
      <div class="flex myType">
        <p @click="changeType(1)" :class="{active: isactive==1}">我发展的商户({{merchantNum}})</p>
        <p
          v-if="hometype !=4 "
          @click="changeType(2)"
          :class="{active: isactive==2}"
        >代理发展商户({{merchantNum2}})</p>
      </div>
      <!-- 我发展的商户 -->
      <div v-if="isactive===1">
        <div v-if="myList" class="flex" style="justify-content: flex-end;padding:10px">
          <p style="margin-right:5px;color:#6A6A6A;">
            销售总额
            <!-- 0（默认）降序   1升序 -->
            <i @click="get_isorder" :class="isorder?'el-icon-caret-bottom':'el-icon-caret-top'"></i>
          </p>
          <p style="color:#6A6A6A;">
            佣金总额
            <!-- 0（默认）降序   1升序 -->
            <i @click="get_isorder2" :class="isorder2?'el-icon-caret-bottom':'el-icon-caret-top'"></i>
          </p>
        </div>
        <ul v-if="myList">
          <li class="flex myLi" v-for="(item,index) in myList" :key="index">
            <div>
              <p style="font-size:14px;">{{item.storeName }}</p>
              <p class="ywy">业务员：{{item.managerName }}</p>
            </div>
            <div class="flex">
              <div>
                <p>
                  <span class="hui">销售总额：</span>
                  <span>￥{{item.countPayPrice }}</span>
                </p>
                <p>
                  <span class="hui">佣金总额：</span>
                  <span class="lan">￥{{item.commissionMoney }}</span>
                </p>
              </div>
              <p style="margin-left:5px" @click="to_myDetail(item.merchant_id)">
                <i class="el-icon-arrow-right"></i>
              </p>
            </div>
          </li>
        </ul>
        <p v-if="!myList" style="margin:30px 0;color:#ccc;font-size:10px">
              <i class="el-icon-warning"></i>暂无数据
        </p>
      </div>
      <!-- 代理发展的商户 -->
      <div v-if="isactive===2">
        <div v-if="agencyList" class="flex" style="padding:10px">
          <el-select
            @change="getType"
            style="width:27%"
            size="mini"
            v-model="value"
            placeholder="请选择"
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <div class="flex">
            <p style="margin-right:5px;color:#6A6A6A;">
              销售总额
              <i
                @click="get_isorder3"
                :class="isorder3?'el-icon-caret-bottom':'el-icon-caret-top'"
              ></i>
            </p>
            <p style="color:#6A6A6A;">
              商户数
              <i
                @click="get_isorder4"
                :class="isorder4?'el-icon-caret-bottom':'el-icon-caret-top'"
              ></i>
            </p>
          </div>
        </div>
        <ul v-if="agencyList">
          <li class="flex myLi" v-for="(item,index) in agencyList" :key="index">
            <div>
              <p style="font-size:14px;">{{item.companyName }}</p>
              <p class="ywy" v-if="item.companyType===1">服务商</p>
              <p class="ywy" v-if="item.companyType===2">一级代理</p>
              <p class="ywy" v-if="item.companyType===3">二级代理</p>
              <p class="ywy" v-if="item.companyType===4">三级代理</p>
              <p class="ywy" v-if="item.companyType===-1">超级服务商</p>
              <p
                class="ywy"
                v-if="item.companyType!=1&&item.companyType!=2&&item.companyType!=3&&item.companyType!=4&&item.companyType!=1"
              >普通</p>
            </div>
            <div class="flex">
              <div>
                <p>
                  <span class="hui">销售总额：</span>
                  <span class="hui">￥{{item.countPayPrice }}</span>
                </p>
                <p>
                  <span class="hui">商户数：</span>
                  <span class="lan">{{item.merchantNum}}</span>
                </p>
              </div>
              <p style="margin-left:5px" @click="to_agencyDetail(item)">
                <i class="el-icon-arrow-right"></i>
              </p>
            </div>
          </li>
        </ul>
         <p v-if="!agencyList" style="margin:30px 0;color:#ccc;font-size:10px">
              <i class="el-icon-warning"></i>暂无数据
        </p>
      </div>
    </div>
  </div>
</template>
<script>
import { myApi } from "@/api";
import * as types from "@/router/types";
import { afterLoginInfoLocal } from "@/storage";
export default {
  data() {
    return {
      activeName: "second",
      isorder: true,
      isorder2: true,
      isorder3: true,
      isorder4: true,
      myList: [],
      isactive: 1,
      payPriceType: 0,
      payPriceType2: 0,
      commissionType: 0,
      merchantNumType: 0,
      options: [
        {
          value: 5,
          label: "全部"
        },
        {
          value: 2,
          label: "一级代理"
        },
        {
          value: 3,
          label: "二级代理"
        },
        {
          value: 4,
          label: "三级代理"
        }
      ],
      value: "",
      agencyList: [],
      hometype: this.$route.query.type,
      type: 5,
      merchantNum: 0,
      merchantNum2: 0,
      noData:false,
      noData2:false
    };
  },
  created() {
    let userInfo = afterLoginInfoLocal.getJSON();
    this.companyId = userInfo.companyId;
    this.getmyMerchant();
    this.getagencyMerchant();
  },
  methods: {
    getType(val) {
      console.log(val, "val");
      this.type = val;
      this.getagencyMerchant();
    },
    get_isorder() {
      this.isorder = !this.isorder;
      if (this.isorder) {
        this.payPriceType = 0;
      } else {
        this.payPriceType = 1;
      }
      this.getmyMerchant();
    },
    get_isorder2() {
      this.isorder2 = !this.isorder2;
      if (this.isorder2) {
        this.commissionType = 0;
      } else {
        this.commissionType = 1;
      }
      this.getmyMerchant();
    },
    get_isorder3() {
      this.isorder3 = !this.isorder3;
      if (this.isorder3) {
        this.payPriceType2 = 0;
      } else {
        this.payPriceType2 = 1;
      }
      this.getagencyMerchant();
    },
    get_isorder4() {
      this.isorder4 = !this.isorder4;
      if (this.isorder4) {
        this.merchantNumType = 0;
      } else {
        this.merchantNumType = 1;
      }
      this.getagencyMerchant();
    },
    //我发展的商户
    getmyMerchant() {
      myApi
        .myMerchant(this.companyId, this.payPriceType, this.commissionType)
        .then(
          res => {
            this.myList = res.obj.listVoList;
            this.merchantNum = res.obj.merchantNum;
          },
          err => {
            this.$toast.error(err.msg);
          }
        );
    },
    //代理的商户
    getagencyMerchant() {
      myApi
        .agencyMerchant(
          this.companyId,
          this.payPriceType2,
          this.merchantNumType,
          this.type
        )
        .then(
          res => {
            this.agencyList = res.obj.listVoList;
            this.merchantNum2 = res.obj.merchantNum;
          },
          err => {
            this.$toast.error(err.msg);
          }
        );
    },
    goback() {
      this.$router.back(-1);
    },
    to_agencyDetail(item) {
      this.$router.push({
        name: types.AGENCYDETAIL,
        query: {
          companyId: item.companyId,
          companyName: item.companyName,
          companyType: item.companyType
        }
      });
    },
    to_myDetail(merchant_id) {
      this.$router.push({
        name: types.MYDETAIL,
        query: { merchant_id: merchant_id }
      });
    },
    changeType(index) {
      this.isactive = index;
    }
  }
};
</script>
<style scoped>
.hui {
  color: #9f9f9f;
}
.lan {
  color: #3791ce;
}
.ywy {
  color: #6f6c6c;
}
.myLi {
  padding: 10px;
  border-bottom: 1px solid #f2f2f2;
}
.myLi p {
  text-align: left;
}
.myType {
  border-bottom: 1px solid #ecebeb;
}
.myType p {
  width: 50%;
  height: 100%;
  padding: 10px;
}
.active {
  color: #3791ce;
  border-bottom: 2px solid #3791ce;
}
.myMerchant {
  background: #fff;
}
.myTitle {
  padding: 10px;
  height: 40px;
}
</style>


